.header {
  position: relative;
}

.contenidog-header {
  position: absolute;
}
.contenidog-header .contenido-header {
  z-index: 10;
  position: relative;
}

.contenidog-header {
  height: 100%;
  width: 100%;
}

.contenido-header {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.contenido-header .imagen-personal img {
  border-radius: 150px;
}
.contenido-header h2 {
  color: white;
}
.contenido-header h2 span {
  color: rgb(56, 196, 243);
  animation: parpadeoNombre 2s linear infinite;
}
.contenido-header p {
  color: white;
  font-weight: 700;
}

.header_img {
  background-image: url("/build/img/imgprogramacion.webp");
  background-size: cover;
  height: 950px;
}

@keyframes parpadeoNombre {
  0%, 10%, 15%, 20%, 35%, 40%, 50%, 55%, 58%, 59% {
    opacity: 1;
  }
  30%, 45%, 0%, 90%, 100% {
    opacity: 0;
  }
}
/** Media Queries **/
ul {
  list-style: none;
  padding: 0;
  width: 100%;
  text-align: center;
}
ul li {
  padding: 0.7rem 0;
}
ul li:hover {
  border-left: 3px solid #0404cd;
  transition: border 0.5s;
}

.contenido-navegacion {
  position: absolute;
  z-index: 10000;
  width: 100%;
  height: 100%;
  /*NAVEGACION*/
  /*TABLET NAVEGACION*/
}
.contenido-navegacion .navegacion {
  transition: all 0.5s ease-in-out;
  position: relative;
  background-color: rgba(28, 28, 28, 0.5294117647);
  visibility: hidden;
  max-width: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  opacity: 0;
}
.contenido-navegacion .navegacion a {
  color: white;
  font-weight: 600;
  padding: 1rem 0;
  letter-spacing: 0.5px;
  font-size: 20px;
}
.contenido-navegacion .navegacion.visible {
  visibility: visible;
  opacity: 1;
  max-width: 100%;
  transition: all 0.5s ease-in-out;
}
@media (min-width: 768px) {
  .contenido-navegacion .navegacion.visible {
    max-width: 300px;
  }
}

.toggle {
  position: absolute;
  top: 5%;
  left: 3%;
  background-color: transparent;
  color: white;
  border: none;
  font-size: 25px;
}
.toggle:hover {
  cursor: pointer;
}
.toggle.novisible {
  visibility: hidden;
}

.close {
  position: absolute;
  background-color: transparent;
  color: white;
  border: none;
  font-size: 25px;
  top: 5%;
  left: 85%;
}
.close:hover {
  cursor: pointer;
}

.sobre-mi {
  height: 809px;
  background-color: rgb(19, 19, 19);
  padding: 4rem 0;
}

.contenedorg-sobremi {
  position: relative;
  z-index: 99;
  height: 100%;
}

.contenedor-sobremi {
  height: 100%;
  width: 100%;
}
.contenedor-sobremi .grid-sobremi {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.contenedor-sobremi .grid-sobremi h2, .contenedor-sobremi .grid-sobremi p {
  color: white;
  padding: 0 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .contenedor-sobremi .grid-sobremi {
    display: grid;
    place-items: center;
    grid-template-columns: repeat(2, 1fr);
  }
  .contenedor-sobremi .grid-sobremi h2, .contenedor-sobremi .grid-sobremi p {
    padding: 0;
    text-align: left;
  }
}

.sobremi-img {
  width: 350px;
}

/*PARTICULAS*/
#particles-js {
  position: absolute;
  background-color: rgb(19, 19, 19);
  height: 800px;
  width: 100%;
}

.tecnologias {
  -webkit-box-shadow: 0px 0px 5px 0px rgb(176, 176, 176);
  -moz-box-shadow: 0px 0px 5px 0px rgb(176, 176, 176);
  box-shadow: 0px 0px 5px 0px rgb(176, 176, 176);
  padding: 5rem 0;
}
.tecnologias h2 {
  text-align: center;
  margin-top: 0;
  text-transform: uppercase;
}
.tecnologias .contenedor-vermas {
  margin: 7rem 0 0 0;
  text-align: center;
}
.tecnologias .contenedor-vermas a {
  color: black;
  padding: 1.4rem 2.5rem;
  border: 2px solid blue;
  color: black;
}
.tecnologias .contenedor-vermas a:hover {
  background-color: blue;
  transition: all 0.5s;
  color: white;
  font-size: 2.1rem;
}

.contenedor-tecnologias {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .contenedor-tecnologias {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 2rem;
  }
}
.contenedor-tecnologias div:nth-of-type(3) {
  border-radius: 20px;
}

.logos {
  width: 250px;
  margin: 0 auto;
}
.logos:hover {
  transform: rotate(30deg) scale(1.1);
  transition: transform 0.8s;
  cursor: pointer;
}
.logos img {
  border-radius: 30px;
}

.footer {
  background: rgb(1, 7, 29);
  background: linear-gradient(270deg, rgb(1, 7, 29) 0%, rgb(0, 1, 10) 100%);
  padding: 1rem;
  text-align: center;
}
.footer p {
  color: white;
  margin: 0;
  animation: parpadeo 2s linear infinite;
  transition: 0.5s ease;
}

@keyframes parpadeo {
  0%, 41%, 45%, 47%, 100% {
    opacity: 1;
    text-shadow: 0px 0px 10px rgb(0, 76, 243);
  }
  42%, 44%, 46%, 48%, 49.5%, 50% {
    opacity: 0;
    text-shadow: none;
  }
}
.proyectos {
  padding: 3rem 0;
}

.proyecto {
  padding-bottom: 2rem;
  margin: 1rem 0;
  -webkit-box-shadow: 0px 0px 5px 0px rgb(176, 176, 176);
  -moz-box-shadow: 0px 0px 5px 0px rgb(176, 176, 176);
  box-shadow: 0px 0px 5px 0px rgb(176, 176, 176);
  border-radius: 18px;
}
.proyecto .img-proyecto img {
  border-radius: 15px;
}

.contenedorg-proyectos h2 {
  text-align: center;
}

.contenedor-proyectos {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .contenedor-proyectos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 3rem;
  }
}

.titulo-proyecto h3 {
  text-align: center;
  margin-bottom: 0;
}
.titulo-proyecto h3 a {
  color: rgba(0, 0, 0, 0.856);
}

.contenedor-ver {
  margin: 1rem 0;
  text-align: center;
}
.contenedor-ver a {
  color: rgba(0, 0, 0, 0.856);
  border: 2px solid black;
  padding: 1.2rem;
}
.contenedor-ver a:hover {
  background-color: black;
  color: white;
  transition: all 0.3s ease-in-out;
}

.texto-proyecto {
  padding: 0.3rem;
}
.texto-proyecto p {
  font-size: 15px;
  padding: 0 1rem;
  text-align: center;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
  margin: 0;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3, h4, h5, h6, p, a {
  font-family: "Monserrat", sans-serif;
}